// header
import commonHeader from "@/components/common-header/common-header";
// tabbar
import tabbar from"@/components/common-tabbar/common-tabbar";
export default {
	data() {
		const currentDate = this.getDate({
			format: true
		})
		return {
			headerTitl:"我的星币",
			listData:[
				{"id":"01","title":"提现","feiyong":"","date":"12月30日 08:11","price":"34.00"},
				{"id":"02","title":"转账","feiyong":"","date":"12月30日 08:11","price":"34.00"},
				{"id":"03","title":"星币兑换","feiyong":"2","date":"12月30日 08:11","price":"134.00"},
				{"id":"04","title":"提现","feiyong":"","date":"12月30日 08:11","price":"34.00"},
				{"id":"05","title":"提现","feiyong":"2","date":"12月30日 08:11","price":"4.00"},
				{"id":"06","title":"提现","feiyong":"","date":"12月30日 08:11","price":"40.00"},
				{"id":"07","title":"提现","feiyong":"2","date":"12月30日 08:11","price":"100.00"}
			],
			// 日期选择
			date: currentDate,
		}
	},
	methods: {
		// 日期选择
		bindDateChange: function(e) {
			var arr = e.detail.value.split('-');
			this.date = `${arr[0]}年${arr[1]}月${arr[2]}日`;
		},
		getDate(type) {
			const date = new Date();
			let year = date.getFullYear();
			let month = date.getMonth() + 1;
			let day = date.getDate();
		
			if (type === 'start') {
				year = year - 60;
			} else if (type === 'end') {
				year = year + 2;
			}
			month = month > 9 ? month : '0' + month;;
			day = day > 9 ? day : '0' + day;
			return `${year}年${month}月${day}日`;
		}
	},
	components:{
		commonHeader,
		tabbar
	},
	computed: {
		startDate() {
			return this.getDate('start');
		},
		endDate() {
			return this.getDate('end');
		}
	},
}
